import { createProtocolHandler } from '@castify/record-mv3/chrome-messaging';

export type OffscreenProtocolDefinition = {
  AUDIO_COUNTDOWN_TICK: {
    payload: Record<string, never>;
    response: void;
  };
  AUDIO_COUNTDOWN_DONE: {
    payload: Record<string, never>;
    response: void;
  };
  RECORDING_CONTROLLER_START: {
    payload: {
      deviceId: string;
    };
    response: void;
  };
  VIDEO_ON: {
    payload: {
      deviceId: string;
    };
    response: void;
  };
  VIDEO_OFF: {
    payload: Record<string, never>;
    response: void;
  };
  RECORDING_CONTROLLER_STOP: {
    payload: Record<string, never>;
    response: void;
  };
  RECORDING_CONTROLLER_DELETE: {
    payload: Record<string, never>;
    response: void;
  };
  STOP_PIP: {
    payload: unknown;
    response: void;
  };
};

const videoElement: HTMLVideoElement = document.createElement('video');
document.body.appendChild(videoElement);
let streamActive = false;

const log = async (...args: any[]): Promise<void> => {
  chrome.runtime.sendMessage({
    target: 'background',
    type: 'log',
    data: args,
  });
};

const initiateMediaCapture = (deviceId: string): void => {
  if (!streamActive && !document.pictureInPictureElement) {
    navigator.mediaDevices
      .getUserMedia({
        video: { deviceId: deviceId ? { exact: deviceId } : undefined },
      })
      .then((stream) => {
        videoElement.srcObject = stream;
        streamActive = true;
        videoElement.play().then(() => {
          handlePiP();
        });
      })
      .catch((error) => {
        log('Stream Error:', error);
        streamActive = false;
      });
  }
};

const handlePiP = (): void => {
  videoElement
    .requestPictureInPicture()
    .then(() => {
      videoElement.addEventListener('leavepictureinpicture', () => {
        log('leave picture in picture');
      });
    })
    .catch((error) => {
      log('PiP Error:', error);
    });
};

const stopPiP = (): void => {
  if (document.pictureInPictureElement) {
    document.exitPictureInPicture().catch((error) => {
      log('Error exiting PiP:', error);
    });
  }
  if (videoElement.srcObject) {
    const tracks = (videoElement.srcObject as MediaStream).getTracks();
    tracks.forEach((track: MediaStreamTrack) => track.stop());
    videoElement.srcObject = null;
    streamActive = false;
  }
};

const protocolHandler = createProtocolHandler<OffscreenProtocolDefinition>({
  AUDIO_COUNTDOWN_TICK: async () => {
    const audio = new Audio(
      chrome.runtime.getURL('assets/audio/countdown-tick.mp3'),
    );
    await audio.play().catch((e) => log('Audio play error:', e));
  },
  AUDIO_COUNTDOWN_DONE: async () => {
    const audio = new Audio(
      chrome.runtime.getURL('assets/audio/countdown-done.mp3'),
    );
    await audio.play().catch((e) => log('Audio play error:', e));
  },
  RECORDING_CONTROLLER_START: async (message) => {
    if (message.payload.deviceId) {
      initiateMediaCapture(message.payload.deviceId);
    }
  },
  VIDEO_ON: async (message) => {
    if (!streamActive) {
      initiateMediaCapture(message.payload.deviceId);
    } else {
      handlePiP();
    }
  },
  VIDEO_OFF: stopPiP,
  RECORDING_CONTROLLER_STOP: stopPiP,
  RECORDING_CONTROLLER_DELETE: stopPiP,
  STOP_PIP: stopPiP,
});

window.onload = () => {
  chrome.runtime.onMessage.addListener(protocolHandler);
};
